<template>
	<div>
		<div class="container center banner">
			<img src="../../static/img/banner.jpg" class="img-responsive pc-bg" />
			<img src="../../static/img/m-banner.jpg" class="img-responsive m-bg" />
		</div>
		<div class="container center info-content">
			<div class="col-lg-3 col-md-3 col-sm-12 col-xs-12">
				<!--pc-->
				<div class="pc-item-nav">
					<div class="zhaosheng zhaosheng1">
						<a href="#">
							<img src="../../static/img/zhaosheng1.png" class="img-responsive" />
						</a>
						<a href="#">
							<img src="../../static/img/zhaosheng2.png" class="img-responsive" />
						</a>
						<router-link to="/campus_style">
							<img src="../../static/img/zhaosheng3.png" class="img-responsive" />
						</router-link>
					</div>
				</div>
			</div>
			<div class="col-lg-9 col-md-9 col-sm-12 col-xs-12 info">
				<div class="info-nav teacher-nav">
					<ul>
						<li><router-link to="/teachers1"><i class="iconfont icon-jisuanqi"></i>会计类老师</router-link></li>
						<li><router-link to="/teachers"><i class="iconfont icon-sheji"></i>设计类老师</router-link></li>
						<li class="active"><router-link to="/teachers3"><i class="iconfont icon-jisuanjishuiping"></i>计算机类老师</router-link></li>
					</ul>
				</div>
				<div class="teacher-box">
					<div class="col-lg-3 col-md-3 col-sm-6 col-xs-6" v-for="item in teacherList">
