							<img :src="item.headimg" class="img-responsive"  />
							<div class="teacher-info">
								<p><span class="name">{{item.name}}</span><span>| {{item.professor}} </span></p>
								<p class="pc-bg">{{item.teachexp}}&nbsp{{item.workexp}}</p>
