									<span>{{item.teachexp}}</span>
									<span>{{item.workexp}}</span>
								</p>
							</div>
						</div>
					</div>
					
				</div>
			</div>
		</div>
	</div>
</template>

<script>
var swiper;
export default {
	data(){
        return{
        	teacherList:[]
        }
    },
    created:function(){
    	document.title="师资力量_扬州中瑞酒店职业学院互联网学院";
    	this.uri = this.$route.path.slice(1);
    	var _self = this;
　　　	var url="http://hh.c1903.com/index/api/teacher";            
		  this.$http.post(url,{token:'xueyuanyi',code:'1a11c6a0e49a916429c1719fa8d587e2'}
			).then(function (data) {
        	var json = data.body;          
        	for (var i=0;i<json.length;i++) {
        		if(json[i].type == 8){
        			_self.teacherList.push(json[i]);
        		}
        	}
		  
    	});  
    },
	activated(){
		 document.documentElement.scrollTop = 0
	}
}	
</script>

